<template>
    <div class="spbox" >
        <a href="#" @click="funi()">关闭</a>
        <form >
            <input type="text" @focus="bool=!bool"  @blur="bool=!bool" 
            @keydown.enter="fund()" v-model="text">
            <span v-if="bool"></span>           
        </form>
    </div>
</template>
<script>
export default {
    methods:{
        funi(){
            this.$router.go(-1)
        },
        fund(){
            // this.$router.push("/linkd")
            // 在子组件抛出自定义事件监听
            this.$emit("zipao",this.bool,this.text)
            }        
    },
    data(){
        return{
            bool:true,
            text:""
        }
    }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .spbox{
        font-size: 0;
        display: flex;
        margin-top: 0.1rem;
        padding-bottom:0.1rem;
        border-bottom:0.01rem solid #f3f3f3
    }
    .spbox>a{
        font-size: 0.17rem;
        color:#42bd56;
        margin-left: 0.2rem;
        margin-top: 0.1rem;

    }
    form>input{
        font-size: .2rem;
        margin-left:0.1rem;
        width:2.3rem;
        height:0.4rem;
        border:none;
        /* outline:none; */
        border:0.01rem solid #f2f2f2;
        background:#f3f3f3;
        position: relative;
    }
    form>span{
        position: absolute;
        top:0.2rem;
        left:1.5rem;
        display: inline-block;
        width: 0.4rem;
        height:0.4rem;
        background-image: url(data:image/png;base64,);
        background-repeat:no-repeat;
        background-size: 60%;
    }

</style>



