<template>
    <div class="boxa">
        <div class="person">
                <router-link to="/login">
                    <img src="../../../static/img/log.png" alt="" v-if="txt">
                    <img src="../../../static/img/p.jpg" alt="" v-else>   
                     <b  v-if="txt">{{txt}}</b>
                    <b  v-else >请先登录</b>                   
                </router-link> 
                <span></span>
                <span></span>
        </div>           
    </div>
</template>
<script>
export default {
    data(){
        return{
            txt:""
        }
    },
    created(){
        var getname=window.localStorage.getItem("token")
        if(getname){
            this.axios({
                method:"get",
                url:"http://localhost:4000/index",
                params:{
                    token:getname
                }
            }).then((ok)=>{
                if(ok.data.linkid==5){
                    this.txt=ok.data.username
                }
            })
        }
    }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .boxa{
       border-bottom: 0.01rem solid #e8e8e8;
    }
    .person{
        margin:1.35rem auto 0;
        width: 90%;
        height:0.72rem;
        font-size: 0;
    }
    .person>a>img{
        width: 0.48rem;
        height:0.48rem;
        border-radius:50%;
        vertical-align: middle;
    }
    b{
        display: inline-block;
        font-size: 0.14rem;
        color:#AAAAAA; 
        vertical-align: middle;
        font-weight: 100;
       margin: 0  0.9rem 0 0.1rem; 
    }
    .person>span:nth-of-type(1){
        display: inline-block;
        width: 0.28rem;
        height:0.28rem;
        vertical-align: middle;
        background-image: url(data:image/svg+xml;base64,);
    }
    .person>span:nth-of-type(2){
        display: inline-block;
        width: 0.28rem;
        height:0.28rem;
        margin-left: 0.25rem;
        vertical-align: middle;
        background-image: url(data:image/svg+xml;base64,);
    }
</style>



